<template>
    <v-container>
        <v-flex align-self-center>
            {{ competences }}
        </v-flex>
    </v-container>
</template>

<script>
    import axios from "axios";

    export default {
        name: "competences",
        data: function() {
            return {
                details_file: "files/details.json",
                competences: null
            };
        },

        methods: {
            getComp: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.competences = result.data.competences;
                });
            }
        },

        mounted: function() {
            this.getComp();
        }
    }
</script>

