<template>
    <v-container>
        <v-flex>
            {{ experiences }}
        </v-flex>
    </v-container>
</template>

<script>
    import axios from "axios";

    export default {
        name: "experiences",
        data: function() {
            return {
                details_file: "files/details.json",
                experiences: null
            };
        },

        methods: {
            getExp: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.experiences = result.data.experiences;
                });
            }
        },

        mounted: function() {
            this.getExp();
        }
    }
</script>

